// Timer active Sale
function timer(hour = '00:00:00') {
  if (typeof hour === 'number' && hour > 0) {
    let value = hour;
    let milliseconds = hour * 60 * 60 * 1000;

    const timerEl = document.getElementById('timer');
    const time = timerEl.querySelector('.hero__timer--time');

    function updateTimer() {
      const hours = Math.floor(milliseconds / (1000 * 60 * 60));
      const minutes = Math.floor((milliseconds % (1000 * 60 * 60)) / (1000 * 60));
      const seconds = Math.floor((milliseconds % (1000 * 60)) / 1000);

      const addZero = n => String(n).padStart(2, '0');

      value = `${addZero(hours)}:${addZero(minutes)}:${addZero(seconds)}`;

      if (timerEl && time) time.textContent = value;

      milliseconds -= 1000;

      if (milliseconds === 0) clearInterval(timer);
    }

    const timer = setInterval(updateTimer, 1000);
  }
  return;
}

timer(5);

// Change price Sale
function sale(oldPrice, newPrice) {
  const saleEl = document.getElementById('sale');

  if (saleEl) {
    const oldEl = saleEl.querySelector('.hero__sale--old');
    const newEl = saleEl.querySelector('.hero__sale--new');

    if (oldEl && newEl) {
      if (oldPrice && newPrice) {
        oldEl.textContent = oldPrice;
        newEl.textContent = newPrice;
      } else {
        oldEl.textContent = '';
        newEl.textContent = '';
      }
    }
  }
  return [oldPrice, newPrice];
}

sale('R 250.00', 'R 160.00');

// Swiper Slider
const swiperProducts = new Swiper('.products__slider', {
  speed: 800,
  loop: true,
  spaceBetween: '1.5%',
  autoplay: true,
  on: {
    slideChange(swiper) {
      $('[data-tab]').each(function (index) {
        $(this).removeClass('active');
        if (index === swiper.realIndex) $(this).addClass('active');
      });
    },
  },
});

// Toggle Tabs Slider
$('[data-tab]').each(function (index) {
  $(this).on('click', function () {
    $('[data-tab]').each(function () {
      $(this).removeClass('active');
    });
    $(this).addClass('active');
    swiperProducts.slideTo(index);
  });
});

// Custom Select (JQuery-UI)
$(function () {
  $('#color').selectmenu();
  $('#size').selectmenu();
});
